@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.gpt3__blog {
  display: flex;
  flex-direction: column;
}

.gpt3__blog-heading {
 width: 100%;
 text-align: left;
 margin-bottom: 5rem;
}

.gpt3_blog-heading h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
}

.gpt3__blog-container {
  display: flex;
  flex-direction: row;
 }

.gpt3__blog-container_groupA {
  flex: 0.75 1;
  margin-right: 2rem;
}

.gpt3__blog-container_groupB {
  flex: 1 1;
  /* margin-right: 2rem; */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
  .gpt3__blog-container {
    flex-direction: column-reverse;
  }

  .gpt3__blog-container_groupA {
    margin: 2rem 0;
  }

  .gpt3__blog-container_groupA .gpt3__blog-container_article {
    width: 48%;
  }

  .gpt3__blog-container_groupA .gpt__blog-container_article-image {
    height: 250px;
  }
}

@media screen and (max-width: 700px) {

  .gpt3__blog-container_groupB {
    grid-template-columns: repeat(1, 1fr);
  }

  .gpt3__blog-container_groupA .gpt3__blog-container_article {
    width: 100%;
  }
  
  .gpt3__blog-heading h1 {
    font-size: 46px;
    line-height: 52px;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__blog-heading h1 {
    font-size: 34px;
    line-height: 42px;
  }
 }
.gpt3__blog-container_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt__blog-container_article-image {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}

.gpt__blog-container_article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container_article-content p {
  font-family: var(--font-family);
  font-weight: bold;
  font-size: 11.649px;
  line-height: 35px;
  color: #fff;

}

.gpt3__blog-container_article-content h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 25.11px;
  line-height: 30px;
  color: #fff;
  
  margin-bottom: 5rem;
  cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child {
  cursor:pointer
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-content h3 {
    font-size: 18px;
    line-height: 25px;
  }

}

.gpt3__blog-container_article-content p a {
  background: var(--gradient-text) ;
  background-clip: text ;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

}
.gpt3__brand {
  display: flex;
  flex-wrap: flex;
  justify-content: center;
  text-align: center;

}
.gpt3__brand div {
  flex: 1 1;
  max-width: 150px;
  min-width: 120px;
  margin: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  
}


.gpt3__cta {
  display: flex;
  justify-content: space-between  ;
  align-items: center;
  flex-direction: row;

  padding: 2rem;
  margin: 4rem;
  border-radius: 1rem;
  background: var(--gradient-bar);
}

.gpt3__cta-content p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: black;
}
.gpt3__cta-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
  color: black;
}

.gpt3__cta-content-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;

}

.gpt3__cta-content-btn button {
  background-color: black;
  color: #FFF;
  border-radius: 40px;
  font-family: var(--font-family);
  font-weight: bold;
  line-height: 30px;
  font-size: 18px;
  padding: 0.5rem 1rem;

  border: none;
  outline: none;
  cursor: pointer;
  min-width: 150px;
}

@media screen and (max-width: 650px) {
  .gpt3__cta {
    flex-direction: column;
  }
  
}

@media screen and (max-width: 550px) {
  .gpt3__cta {
    flex-direction: column;
    margin: 4rem 2rem;
  }
  .gpt3__cta-content h3 {
    font-size: 18px;
    line-height: 32px;
  }

  .gpt3__cta-content-btn button {
   font-size: 14px;
   line-height: 28px;
  }
}
.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  
  padding: 2rem 6rem;
}

.gpt3__navbar-menu_container-links p a {
  color: #FFF;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-links {
  flex: 1 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.gpt3__navbar-links-logo { 
  display: flex;
  justify-content: flex-start;
  align-items: center; 
  
}

.gpt3__navbar-links-logo img {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 25%;
  height: 25%;
  cursor: pointer;
}

.gpt3__navbar-links-container {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;

}


.gpt3__navbar-links-container p, 
.gpt3__navbar-menu_container p {
  justify-content: flex-end;
  color: #FFF;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-sign button,
.gpt3__navbar-menu_container button  {
  padding: 1rem 1.5rem;
  color: #FFF;
  background: #FF4820;
  font-family: var(--font-family);
  font-weight: 500;
  left: 1153px;
  top: 55px;
  font-size: 18px;
  line-height: 25px;
  border-radius: 5px;
  border: none;
  outline: none;
  cursor: pointer;
}

.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;


}

.gpt3__navbar-menu svg {
  cursor: pointer;

}

.gpt3__navbar-menu_container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  flex-direction: column;

  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 210px;
  border-radius: 5px;
  box-shadow: 0 0 5 rgb(0, 0, 0, 0.2);

}

.gpt3__navbar-menu_container p {
  margin: 1rem 0;
}
.gpt3__navbar-menu_container-links-sign {
  display: none;
}

@media screen and ( max-width: 1050px ) {
.gpt3__navbar-links-container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
}

@media screen and ( max-width: 700px ) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
  
}

@media screen and (max-width: 550px ) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign {
    display: none;

  }
  .gpt3__navbar-links-container {
    top: 20px;

  }
  .gpt3__navbar-links-container-links-sign {
    display: block;
  }
}
